.box {
  list-style: none;
  background: #f1f1f1;
  border: 1px solid #ccc;
  float: left;
  box-sizing: border-box;
  width: 185px;
  height: 185px;
  padding: 0;
  text-align: center;
}

.box>span {
  font-size: 150px;
}

.player-x {
  color: #388e3c;
}

.player-o {
  color: #ff3399;
}

.box.cursor-player-x:hover {
  cursor: url(icons/cross.png), pointer;
}

.box.cursor-player-o:hover {
  cursor: url(icons/circle.png), pointer;
}

.box.cursor-moving-disabled:hover {
  cursor: not-allowed;
}

.box.moving-enabled:hover {
  background: #fff;
}

.box.moving-disabled:hover {
  background: #f1f1f1;
}

.box.winning-box, .box.winning-box:hover {
  background: #ffff99;
  cursor: not-allowed;
}

@media(max-width:900px) {
  .box {
    width: 150px;
    height: 150px;
  }
}

@media(max-width:700px) {
  .box {
    width: 100px;
    height: 100px;
  }
  .box>span {
    font-size: 100px;
  }
}