html {
    min-height: 100%;
    background: linear-gradient(#99CCCC, #9966ff);
}

body {
    color: #fff;
    font-weight: bold;
    text-align: center;
}

h2 {
    color: #9977ff;
}

@media screen and (max-width: 770px) {
    h2 {
        color: #660033;
    }
}
.container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

header {
    width: 100%;
    color: #9966CC;
    font-size: 25px;
}

.left {
    -webkit-order: 1;
            order: 1;
    width: 250px;
}

article {
    -webkit-order: 2;
            order: 2;
    max-width: 600px;
    padding-top: 1px;
    margin-top: 5px;
    margin-left: 1px;
    margin-right: 1px;
}

.right {
    -webkit-order: 3;
            order: 3;
    width: 250px;
}

footer {
    -webkit-order: 4;
            order: 4;
    width: 100%;
    color: #9977ff;
    text-shadow: black 0.1em 0.1em 0.2em;
    padding-top: 1px;
}

@media(max-width:1000px){
    .left{
        width: 160px;
    }
    .right{
        width: 160px;
    }
}

@media(max-width:880px) {
    .left {
        width: 130px;
    }
    .right {
        width: 130px;
    }
}

@media screen and (max-width: 770px) {
    .container {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .left {
        width: 100%;
        font-size: 20px;
        
    }
    .right {
        width: 100%;
        font-size: 15px;
    }
    article {
        margin-left: auto;
        margin-right: auto;
        top: 50%;
        left: 50%;
    }
}
.game-board {
  clear: both;
  display: table;
  border: 1px solid #dadada;
  margin: 0 auto;
  padding: 0;
}

.game-board:focus {
  outline: none;
}
.box {
  list-style: none;
  background: #f1f1f1;
  border: 1px solid #ccc;
  float: left;
  box-sizing: border-box;
  width: 185px;
  height: 185px;
  padding: 0;
  text-align: center;
}

.box>span {
  font-size: 150px;
}

.player-x {
  color: #388e3c;
}

.player-o {
  color: #ff3399;
}

.box.cursor-player-x:hover {
  cursor: url(data:image/png;base64,), pointer;
}

.box.cursor-player-o:hover {
  cursor: url(data:image/png;base64,), pointer;
}

.box.cursor-moving-disabled:hover {
  cursor: not-allowed;
}

.box.moving-enabled:hover {
  background: #fff;
}

.box.moving-disabled:hover {
  background: #f1f1f1;
}

.box.winning-box, .box.winning-box:hover {
  background: #ffff99;
  cursor: not-allowed;
}

@media(max-width:900px) {
  .box {
    width: 150px;
    height: 150px;
  }
}

@media(max-width:700px) {
  .box {
    width: 100px;
    height: 100px;
  }
  .box>span {
    font-size: 100px;
  }
}
.reset-game {
    height: 30px;
    width: 150px;
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    border: none;
    background: #ff99cc;
    color: #fff;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 5px;
}

.reset-game:hover {
    background: #2c3e52;
}
.status>span {
    color: #ed4e6e;
    font-size: 3em;
    line-height: 1.75;
}

.playerInfo {
    color: #9977ff;
}

@media(max-width:880px) {
    .status>span {
        font-size: 2em;
        line-height: 1.35;
    }
}

@media screen and (max-width: 770px) {
    .playerInfo {
        font-size: 17px;
    }
}
.space-between-elem {
    margin-left: 10px;
}

.score-highlight {
    color: #ff6600;
}
